import React, { useState } from 'react'

import { useGetDidAddress, useApplyForList } from '../../state/guard/hooks'
import { useDidContract } from '../../hooks/useContract'
import { ExclamationCircleOutlined } from '@ant-design/icons';

import { Table, Space, Modal } from 'antd'
import { Layout } from 'antd';

const { Content } = Layout;


export default function NftTable() {
  // reset states on back

  const didAddress = useGetDidAddress();
  const didContract = useDidContract(didAddress)

  var dataList = useApplyForList(didContract, 0, 9);

  const [showModalVisible, setModalVisible] = useState(false)
  const [showUserInfo, setUserInfo] = useState(undefined)


  const columns = [
    {
      title: '地址',
      dataIndex: 'owner',
      key: 'owner',
    },
    {
      title: '邮箱',
      dataIndex: 'email',
      key: 'email',
    },
    {
      title: 'NFT发放时间',
      dataIndex: 'applyTime',
      key: 'applyTime',
    },
    {
      title: 'NFT移除时间',
      key: 'approveTime',
      dataIndex: 'approveTime',
    },
    {
      title: '状态',
      key: 'action',
      dataIndex: 'nam2e',
    },
    {
      title: '操作',
      key: 'action',
      render: (t:any) => (
        <Space size="middle">
          <a
            onClick={() => {
              setUserInfo(t)
              setModalVisible(true)
            }}
          >查看信息</a>
          <a
            onClick={() => {
              Modal.confirm({
                title: 'Confirm',
                icon: <ExclamationCircleOutlined />,
                content: '移除白名单会使用户NFT身份失效，并无法再通过DEX进行任何交易，请谨慎操作',
                okText: '确认',
                cancelText: '取消',
                onOk: () => {

                }
              })
            }}
          >移除白名单</a>
        </Space>
      ),
    },
  ];


  return (
    <Content>
      <Table
        columns={columns}
        dataSource={dataList}
        pagination={false}
      />
      <Modal
        title="用户信息"
        style={{ top: 20 }}
        visible={showModalVisible}
        onOk={() => setModalVisible(false)}
        onCancel={() => setModalVisible(false)}
      >
        <p><h4>地址: </h4>{showUserInfo?.['owner']}</p>
        <p><h4>邮箱: </h4>{showUserInfo?.['email']}</p>
        <p><h4>NFT发放时间: </h4>{showUserInfo?.['applyTime']}</p>
        <p><h4>NFT移除时间: </h4>{showUserInfo?.['approveTime']}</p>
        <p><h4>用户状态: </h4>{showUserInfo?.['']}</p>
      </Modal>
    </Content>
  )
}